olor: #292726;\r\n    margin: 3%;\r\n    padding: 5px;\r\n    border: hsl(0, 0%, 60%) solid 2px;\r\n}\r\n\r\n#placeholder {\r\n    background-color: transparent;\r\n    color: #eee;\r\n    font-size: 1.2rem;\r\n    border: none;\r\n    max-width: 160px; \r\n}\r\n\r\n#newTodo {\r\n    line-height: 1.5;\r\n    resize: none;\r\n    margin-left: 10px;\r\n    color: #eee;\r\n    border: none;\r\n    font-size: 1.4rem;\r\n    background-color: transparent;\r\n}\r\n\r\n#addBtn, .new-project-btn, .clear-all-task-btn {\r\n    font-size: 1.5rem;\r\n    width: 60%;\r\n    color: #eee;\r\n    background-color: transparent;\r\n    place-self: center;\r\n    border: 1px solid #ff8576;\r\n}\r\n\r\n#addBtn:hover, #addBtn:focus, .new-project-btn:hover, .new-project-btn:focus, .clear-all-task-btn:hover, .clear-all-task-btn:focus {\r\n    background-color: #ff8576;\r\n    color: #eee;\r\n}\r\n\r\n/* -----------------placeholders--------------------- */\r\n::placeholder {\r\n    color: var(--col, #eee);\r\n}\r\n\r\n/* \r\n::-webkit-input-placeholder {\r\n    text-align: center;\r\n} \r\n*/\r\n \r\n/* :-moz-placeholder {\r\n    text-align: center;  \r\n} */\r\n \r\n/* ::-moz-placeholder {  \r\n    text-align: center;  \r\n} */\r\n \r\n/* :-ms-input-placeholder {  \r\n    text-align: center; \r\n} */\r\n\r\n/* ----------------task---------------------- */\r\n\r\nhr {\r\n    margin: 10px;\r\n    width: 90%;\r\n    color: #eee;\r\n    opacity: 40%;\r\n    place-self: center;\r\n}\r\n\r\n.task-container, .completed-task-container {\r\n    margin: 3%;\r\n    border-bottom: solid 1.5px #ff8576;\r\n    display: grid;\r\n    grid-template-columns: 25px 1fr 30px;\r\n}\r\ninput.checkbox {\r\n    /* -moz-appearance: none;\r\n    appearance: none;\r\n    -webkit-appearance: none; */\r\n    background-color: #ff8576;\r\n    \r\n}\r\n\r\ninput.checkbox:checked {\r\n    border: #eee;\r\n    color: #ff8576;\r\n    background-color: transparent;\r\n}\r\n\r\n.task-text {\r\n    align-self: center;\r\n    font-size: 1.2rem;\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    margin: 10px;\r\n}\r\n\r\n.remove-task-btn {\r\n    background-color: #ff8576;\r\n    color:#292726;\r\n    max-height: 35px;\r\n    /* margin: 10px 10px 10px 5px; */\r\n    padding: 0px 0.3em;\r\n    border: none;\r\n    font-size: 2rem;\r\n    place-self: center;\r\n    grid-column: 3 / 4;\r\n}\r\n\r\n.remove-task-btn:hover, .remove-task-btn:focus {\r\n    box-shadow: 3px 3px 0px #a2554b;\r\n}\r\n\r\n/* ------------------------------ projects--------------------------- */\r\n\r\n.new-project-btn-container, .new-project-container, .clear-all-task-btn-container {\r\n    grid-row: 1 / 2;\r\n    justify-content: center;\r\n    align-items: center;\r\n    margin: 20px 0;\r\n}\r\n\r\n.new-project-btn {\r\n    width: 80%;\r\n    padding: 10px;\r\n}\r\n\r\n.separator {\r\n    grid-row: 2 / 3;\r\n    display: flex;\r\n    align-items: center;\r\n    text-align: center;\r\n    margin-bottom: 1em;\r\n}\r\n\r\n.separator::after,\r\n.separator::before {\r\n    content: ' ';\r\n    flex: 1;\r\n    margin: 0px 20px;\r\n    opacity: 40%;\r\n    border-bottom: 1px solid #eee;\r\n}\r\n.separator:not(:empty)::before {\r\n    margin-right: .25em;\r\n}\r\n.separator:not(:empty)::after {\r\n    margin-left: .25em;\r\n}\r\n\r\n.existing-projects-container {\r\n    grid-row: 3 / 4;\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 0.7em;\r\n}\r\n\r\n.new-project-container {\r\n    display: flex;\r\n    width: 90%;\r\n    padding: 3px;\r\n    justify-self: center;\r\n    border: solid #ff8576 2px;\r\n}\r\n\r\n#new-project-name {\r\n    width: 95%;\r\n    color: #eee;\r\n    background-color: transparent;\r\n    font-size: 1.3rem;\r\n    padding:10px;\r\n    border: none;\r\n}\r\n\r\n.add-new-project-btn {\r\n    font-size: 1.5rem;\r\n    background-color: transparent;\r\n    border: none;\r\n    border-left: 2px solid #ff8576;\r\n    padding: 10px;\r\n    width: 50px;\r\n    color: #eee;\r\n    justify-content: end;\r\n}\r\n\r\n.add-new-project-btn:hover, .add-new-project-btn:focus {\r\n    background-color: #ff8576;\r\n    color: #eee;\r\n}\r\n.project-icon {\r\n    filter: url('./assets/folder-logo.svg');\r\n    width: 30px;\r\n    fill: red;\r\n\r\n    filter: invert(100%) hue-rotate(120deg);\r\n\r\n}\r\n\r\n/* ---------------project------------------- */\r\n.project-container {\r\n    display: grid;\r\n    grid-template-columns: 30px 1fr 30px;\r\n    justify-content: center;\r\n    gap: 5px;\r\n    width: 90%;\r\n    border-left: 1px solid #ff8576;\r\n    padding-left: 5px;\r\n    place-self: center; \r\n    padding-bottom: 10px;     \r\n}\r\n\r\n.project-container:focus, .project-container:hover {\r\n    border-bottom: solid 1px #ff8576;\r\n}\r\n.project-name {\r\n    font-family: 'Syne Mono', monospace;\r\n}\r\n\r\n.delete-project-btn {\r\n    background-color: transparent;\r\n    color: #ff8576;\r\n    font-size: 1.2rem;\r\n    border: 1px solid #ff8576;\r\n    opacity: 50%;\r\n}\r\n\r\n.delete-project-btn:hover, .delete-project-btn:focus {\r\n    opacity: 90%;\r\n    background-color: #ff8576;\r\n    color: #eee;\r\n}\r\n\r\n/* ------------------file---------------------------- */\r\n\r\n#projectFilePage {\r\n    display: grid;\r\n}\r\n\r\n#projectFilePage div.todolist div.task-container {\r\n    grid-template-rows: 1fr minmax(20px);\r\n}\r\n\r\n\r\n#projectFilePage div.todolist div.task-container div.task-folder-container {\r\n    grid-column: 2 / 3;\r\n    grid-row: 2 / 3;\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    justify-self: end;\r\n    width: fit-content;\r\n    padding: 0.05em 0.2em;\r\n    margin-bottom: 0.1em;\r\n    margin-right: 0.1em;\r\n    background-color: #ae4335;\r\n}\r\n\r\n#projectFilePage div.todolist div.task-container div.task-folder-container p.task-folder-name {\r\n    font-size: 0.845rem;\r\n    font-style: italic;\r\n    color: #eee;\r\n    font-weight: 600;\r\n}\r\n\r\n/* ----------completed tasks---------------- */\r\n\r\n.tool-container {\r\n    display: flex;\r\n    flex-direction: column;\r\n    flex-wrap: wrap;\r\n}\r\n\r\n#completedTaskPage {\r\n    display: grid;\r\n    grid-template-rows: 80px 1fr;\r\n}\r\n\r\n.completed-task-container {\r\n    grid-template-columns: 1fr 30px;\r\n}\r\n\r\n.completed-task-text {\r\n    font-size: 1.3rem;\r\n    font-style: italic;\r\n    text-decoration: line-through;\r\n    opacity: 70%;\r\n}\r\n\r\n.clear-all-task-btn-container {\r\n    margin-bottom: 20px;\r\n}\r\n\r\n\r\n/* ------------dialog------------- */\r\n\r\nform.form {\r\n    display: grid;\r\n    grid-template-rows: 1.3em 1fr 2em;\r\n}\r\n\r\ndialog {\r\n    width: max-content;\r\n    justify-content: center;\r\n    background-color: #292726;\r\n    border: #ff8576 solid 1px;\r\n    font-family: monospace;\r\n    top: 50%;\r\n    left: 50%;\r\n    transform: translate(-50%, -50%);\r\n    padding: 5px;\r\n}\r\n\r\ndialog .close-btn-container {\r\n    display: grid;\r\n}\r\n\r\ndialog .close-btn {\r\n    margin-right: 10px;\r\n    font-weight: 700;\r\n    color: #eee;\r\n    font-size: 1.3rem;\r\n    align-self: center;\r\n    border: none;\r\n    justify-self: end;\r\n    background-color: transparent;\r\n}\r\n\r\ndialog .msg-container {\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    padding: 8px;\r\n}\r\n\r\ndialog .msg {\r\n    font-size: 1.4rem;\r\n    text-align: center;\r\n    background-color: transparent;\r\n    color: #eee;\r\n\r\n}\r\n\r\ndialog .confirmation-btn-container {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-evenly;\r\n}\r\n\r\ndialog .cancel-btn, dialog .ok-btn {\r\n    font-size: 1.2rem;\r\n    padding: 0.5em;\r\n    background-color: #ff8576;\r\n    border: none;\r\n}\r\n\r\ndialog .ok-btn:hover, dialog .ok-btn:focus, dialog .cancel-btn:hover, dialog.cancel-btn:focus  {\r\n    box-shadow: 0 0 0 0.125rem #292726, 0 0 0 0.2rem #ff8576;\r\n}\r\n\r\ndialog .close-btn:hover, dialog .close-btn:focus {\r\n    box-shadow: 0 0 0 0.125rem #292726, 0 0 0 0.2rem #eee;\r\n} \r\n\r\n\r\n\r\n\r\n\r\n/* -----------media query--------------- */\r\n\r\n@media only screen and (min-width: 800px ) {\r\n    body {\r\n        height: 100vh;\r\n        grid-template-columns: minmax(200px, 300px) 1fr;\r\n        grid-template-rows: 100px 1fr;\r\n    }\r\n\r\n    div.nav-container {\r\n        grid-area: 1 / 1 / 3 / 2;\r\n        gap: 20px;\r\n        padding-top: 20px;\r\n        align-items: center;\r\n        display: flex;\r\n        margin: 0;\r\n        background-color: #2a2625;\r\n        flex-direction: column;\r\n        border-right: 1px #ff8576 solid;\r\n    }\r\n\r\n    content#content {\r\n        grid-area: 2 / 2 / 3 / 3;\r\n    }\r\n\r\n    header.header {\r\n        grid-column: 2 / 3;\r\n    }\r\n\r\n    .project-btn-container {\r\n        display: flex;\r\n        flex-wrap: wrap;\r\n    }\r\n\r\n\r\n}"],"sourceRoot":""}]);
